<template>
  <div id="index_container">
    <div class="dark-light">
      <svg
        viewBox="0 0 24 24"
        stroke="currentColor"
        stroke-width="1.5"
        fill="none"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        <path d="M21 12.79A9 9 0 1111.21 3 7 7 0 0021 12.79z" />
      </svg>
    </div>
    <div class="app">
      <div class="header">
        <div class="menu-circle"></div>
        <div class="header-menu" @dblclick="dbclick">
          <!-- <a class="menu-link" href="#" @dblclick="dbclick" @click="backToIndex" :class="{'is-active': indexActive}"></a> -->
          <a class="menu-link" href="#" 
            v-for="(item, index) in $store.state.menuList" 
            :key="index" :class="{'is-active': item.active}"
            @click="turnToPage(item.route, item.pid)">{{item.title}}</a>
          <!-- <a class="menu-link" href="#">Discover</a>
          <a class="menu-link" href="#">Market</a> -->
        </div>
        <!-- <div class="search-bar">
          <input type="text" placeholder="Search" />
        </div> -->
        <div class="header-profile" style="padding-right: 0">
          <div class="right_is_login" v-show="account.username != ''">
            <img
              class="profile-img"
              :src="'http://127.0.0.1:8000' + this.account.avatar"
              alt=""
              style="margin-right: 6px"
            />
            <span style="color: #fff;display: inline-block;vertical-align: 11px;margin-right: 24px;">{{account.username}}</span>
            <Tooltip content="" placement="bottom">
              <svg @click="logout" data-v-52b17e8b="" style="width: 28px; height: 28px; fill: currentcolor; overflow: hidden;color: #ccc;cursor:pointer" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1374" class="icon" ><path data-v-52b17e8b="" d="M512 896H256V128h256c19.2 0 32-12.8 32-32s-12.8-32-32-32H259.2C224 64 192 92.8 192 128v768c0 35.2 32 64 67.2 64H512c19.2 0 32-12.8 32-32s-12.8-32-32-32zM822.4 489.6l-192-192c-6.4-6.4-12.8-9.6-22.4-9.6s-16 3.2-22.4 9.6c-12.8 12.8-12.8 32 0 44.8l137.6 137.6H416c-19.2 0-32 12.8-32 32s12.8 32 32 32h307.2l-137.6 137.6c-12.8 12.8-12.8 32 0 44.8s32 12.8 44.8 0l192-192c12.8-12.8 12.8-32 0-44.8z" p-id="1375"></path></svg>
            </Tooltip>
            <!-- <svg @click="logiout" data-v-52b17e8b="" style="width: 28px; height: 28px; fill: currentcolor; overflow: hidden;color: #ccc" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1374" class="icon" ><path data-v-52b17e8b="" d="M512 896H256V128h256c19.2 0 32-12.8 32-32s-12.8-32-32-32H259.2C224 64 192 92.8 192 128v768c0 35.2 32 64 67.2 64H512c19.2 0 32-12.8 32-32s-12.8-32-32-32zM822.4 489.6l-192-192c-6.4-6.4-12.8-9.6-22.4-9.6s-16 3.2-22.4 9.6c-12.8 12.8-12.8 32 0 44.8l137.6 137.6H416c-19.2 0-32 12.8-32 32s12.8 32 32 32h307.2l-137.6 137.6c-12.8 12.8-12.8 32 0 44.8s32 12.8 44.8 0l192-192c12.8-12.8 12.8-32 0-44.8z" p-id="1375"></path></svg> -->

          </div>
          <div class="right_no_login" v-if="account.id == ''">
            <div @click="$refs.leftSide.userForm.data.userForm = 1" style="cursor: pointer">
              <svg class="icon" style="width: 1em;height: 1em;vertical-align: middle;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2671"><path d="M624.112 547.68c9.76-8.768 15.888-21.52 15.888-35.68s-6.144-26.896-15.888-35.68l-160-144a48 48 0 0 0-64.224 71.36L466.912 464H176a48 48 0 1 0 0 96h290.912l-67.024 60.32a48 48 0 1 0 64.224 71.36l160-144zM896 510.912V142.96a48 48 0 0 0-48-48H608a48 48 0 1 0 0 96h192v640H608a48 48 0 1 0 0 96h240a48 48 0 0 0 48-48V510.992v-0.048-0.032z" p-id="2672"></path></svg>
              /
            </div>
          </div>
        </div>
      </div>
      <div class="wrapper">
        <left-side ref="leftSide" />
        <keep-alive >
          <router-view></router-view>
        </keep-alive>
      </div>
      <div class="overlay-app"></div>
    </div>
  </div>
</template>

<script>
import IndexContent from './IndexContent.vue'
import LeftSide from './LeftSide.vue'

export default {
  data() {
    return {
      account: {
        id: '',
        username: '',
        avatar: ''
      },
      indexActive: true
    }
  },
  components: {
    IndexContent,
    LeftSide
  },
  created() {
    if (JSON.parse(localStorage.getItem('userInfo'))) {
      const {id, username, avatar} = JSON.parse(localStorage.getItem('userInfo'))
      this.account.id = id
      this.account.username = username
      this.account.avatar = avatar
    }
    this.$EventBus.$on('isLogin', () => {
      if (JSON.parse(localStorage.getItem('userInfo'))) {
        const {id, username, avatar} = JSON.parse(localStorage.getItem('userInfo'))
        this.account.id = id
        this.account.username = username
        this.account.avatar = avatar
      }
    })
    if (!this.$store.state.menuList.length) {
      this.indexActive = false
    }
  },
  mounted() {
    this.listener()
  },
  methods: {
    listener() {
    //   $(function () {
    //   $(".menu-link").click(function () {
    //     $(".menu-link").removeClass("is-active");
    //     $(this).addClass("is-active");
    //   });
    // });

    // $(function () {
    //   $(".main-header-link").click(function () {
    //     $(".main-header-link").removeClass("is-active");
    //     $(this).addClass("is-active");
    //   });
    // });

    // const dropdowns = document.querySelectorAll(".dropdown");
    // dropdowns.forEach((dropdown) => {
    //   dropdown.addEventListener("click", (e) => {
    //     e.stopPropagation();
    //     dropdowns.forEach((c) => c.classList.remove("is-active"));
    //     dropdown.classList.add("is-active");
    //   });
    // });

    $(".search-bar input")
      .focus(function () {
        $(".header").addClass("wide");
      })
      .blur(function () {
        $(".header").removeClass("wide");
      });

    $(document).click(function (e) {
      var container = $(".status-button");
      var dd = $(".dropdown");
      if (!container.is(e.target) && container.has(e.target).length === 0) {
        dd.removeClass("is-active");
      }
    });

    $(function () {
      $(".dropdown").on("click", function (e) {
        $(".content-wrapper").addClass("overlay");
        e.stopPropagation();
      });
      $(document).on("click", function (e) {
        if ($(e.target).is(".dropdown") === false) {
          $(".content-wrapper").removeClass("overlay");
        }
      });
    });

    $(function () {
      $(".status-button:not(.open)").on("click", function (e) {
        $(".overlay-app").addClass("is-active");
      });
      $(".pop-up .close").click(function () {
        $(".overlay-app").removeClass("is-active");
      });
    });

    $(".status-button:not(.open)").click(function () {
      $(".pop-up").addClass("visible");
    });

    $(".pop-up .close").click(function () {
      $(".pop-up").removeClass("visible");
    });

    const toggleButton = document.querySelector(".dark-light");

    toggleButton.addEventListener("click", () => {
      document.body.classList.toggle("dark-mode");
    });
    },
    // 
    logout() {
      localStorage.clear();
      location.reload()
    },
    backToIndex() {
      this.indexActive = true
      this.$store.state.menuList.map((item, index) => {
        item.active = false
      })
      if (this.$route.name == 'index') {
        return
      }
      this.$router.push({name: 'index'})
    },
    dbclick() {
      document.querySelector('.content-wrapper').scrollTop = 0
    },
    turnToPage(route, pid) {
      this.$store.state.menuList.map((item, index) => {
        if (item.pid === pid) {
          this.$store.commit('CHANGESTATUS', {index, status: true})
        } else {
          this.$store.commit('CHANGESTATUS', {index, status: false})
        }
      })
      this.$router.push({name: route, params: {'pid': pid}}).catch(err => {})
    }
  }
};
</script>

<style lang="less" scoped>
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

#index_container {
  // font-family: var(--body-font);
  font-family: 'Quicksand', sans-serif;
  // background-image: url(https://wallpapershome.com/images/wallpapers/macos-big-sur-1280x720-dark-wwdc-2020-22655.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2em;
  width: 100%;
  height: 100vh;
  background-color: #e493d0;
  background-image: 
    radial-gradient(closest-side, rgb(5, 129, 230), rgba(235, 105, 78, 0)),
    radial-gradient(closest-side, rgb(63, 39, 131), rgba(243, 11, 164, 0)),
    radial-gradient(closest-side, rgb(168, 154, 80), rgba(254, 234, 131, 0)),
    radial-gradient(closest-side, rgba(170, 142, 245, 1), rgba(170, 142, 245, 0)),
    radial-gradient(closest-side, rgb(119, 60, 11), rgba(248, 192, 147, 0));
  background-size: 
    130vmax 130vmax,
    80vmax 80vmax,
    90vmax 90vmax,
    110vmax 110vmax,
    90vmax 90vmax;
  background-position:
    -80vmax -80vmax,
    60vmax -30vmax,
    10vmax 10vmax,
    -30vmax -10vmax,
    50vmax 50vmax;
  background-repeat: no-repeat;
  animation: 10s movement linear infinite;
  @media screen and (max-width: 480px) {
    padding: 0.8em;
  }
}

// #index_container::after {
//   content: '';
//   display: block;
//   position: fixed;
//   width: 100%;
//   height: 100%;
//   top: 0;
//   left: 0;
//   backdrop-filter: blur(10px);
//   -webkit-backdrop-filter: blur(10px);
// }
@keyframes movement {
  0%, 100% {
    background-size: 
      130vmax 130vmax,
      80vmax 80vmax,
      90vmax 90vmax,
      110vmax 110vmax,
      90vmax 90vmax;
    background-position:
      -80vmax -80vmax,
      60vmax -30vmax,
      10vmax 10vmax,
      -30vmax -10vmax,
      50vmax 50vmax;
  }
  25% {
    background-size: 
      100vmax 100vmax,
      90vmax 90vmax,
      100vmax 100vmax,
      90vmax 90vmax,
      60vmax 60vmax;
    background-position:
      -60vmax -90vmax,
      50vmax -40vmax,
      0vmax -20vmax,
      -40vmax -20vmax,
      40vmax 60vmax;
  }
  50% {
    background-size: 
      80vmax 80vmax,
      110vmax 110vmax,
      80vmax 80vmax,
      60vmax 60vmax,
      80vmax 80vmax;
    background-position:
      -50vmax -70vmax,
      40vmax -30vmax,
      10vmax 0vmax,
      20vmax 10vmax,
      30vmax 70vmax;
  }
  75% {
    background-size: 
      90vmax 90vmax,
      90vmax 90vmax,
      100vmax 100vmax,
      90vmax 90vmax,
      70vmax 70vmax;
    background-position:
      -50vmax -40vmax,
      50vmax -30vmax,
      20vmax 0vmax,
      -10vmax 10vmax,
      40vmax 60vmax;
  }
}

.video-bg {
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;

  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

img {
  max-width: 100%;
}

.dark-light {
  position: fixed;
  bottom: 50px;
  right: 30px;
  background-color: var(--dropdown-bg);
  box-shadow: -1px 3px 8px -1px rgba(0, 0, 0, 0.2);
  padding: 8px;
  border-radius: 50%;
  z-index: 3;
  cursor: pointer;
  svg {
    width: 24px;
    flex-shrink: 0;
    fill: #ffce45;
    stroke: #ffce45;
    transition: 0.5s;
  }
}

.light-mode {
  .dark-light svg {
    fill: transparent;
    stroke: var(--theme-color);
  }
  .profile-img {
    border: 2px solid var(--theme-bg-color);
  }
  .content-section ul {
    background-color: var(--theme-bg-color);
  }
  .pop-up__title {
    border-color: var(--theme-color);
  }
  .dropdown.is-active ul {
    background-color: rgb(255 255 255 / 94%);
  }
}

body.light-mode:before,
body.light-mode .video-bg:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.72) 0%,
    rgb(255 255 255 / 45%) 100%
  );
  backdrop-filter: saturate(3);
}

.app {
  background-color: var(--theme-bg-color);
  max-width: 1250px;
  max-height: 860px;
  height: 90vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  border-radius: 14px;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  font-size: 15px;
  font-weight: 500;
}

.header {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  height: 58px;
  width: 100%;
  border-bottom: 1px solid var(--border-color);
  padding: 0 30px;
  white-space: nowrap;
  @media screen and (max-width: 480px) {
    padding: 0 16px;
  }
  &-menu {
    display: flex;
    align-items: center;
    a {
      padding: 20px 30px;
      text-decoration: none;
      color: var(--inactive-color);
      border-bottom: 2px solid transparent;
      transition: 0.3s;
      @media screen and (max-width: 610px) {
        &:not(.main-header-link) {
          display: none;
        }
      }
      &.is-active,
      &:hover {
        color: var(--theme-color);
        border-bottom: 2px solid var(--theme-color);
      }
    }
  }
}

.notify {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    background-color: #3a6df0;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    right: 20px;
    top: 16px;
  }
  @media screen and (max-width: 1055px) {
    display: none;
  }
}

.menu-circle {
  width: 15px;
  height: 15px;
  background-color: #f96057;
  border-radius: 50%;
  box-shadow: 24px 0 0 0 #f8ce52, 48px 0 0 0 #5fcf65;
  margin-right: 195px;
  flex-shrink: 0;
  @media screen and (max-width: 945px) {
    display: none;
  }
}

.search-bar {
  height: 40px;
  display: flex;
  width: 100%;
  max-width: 400px;
  padding-left: 16px;
  border-radius: 4px;
  input {
    width: 100%;
    height: 100%;
    border: none;
    background-color: var(--search-bg);
    border-radius: 4px;
    font-family: var(--body-font);
    font-size: 15px;
    font-weight: 500;
    padding: 0 20px 0 40px;
    box-shadow: 0 0 0 2px rgb(134 140 160 / 2%);
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 56.966 56.966' fill='%23717790c7'%3e%3cpath d='M55.146 51.887L41.588 37.786A22.926 22.926 0 0046.984 23c0-12.682-10.318-23-23-23s-23 10.318-23 23 10.318 23 23 23c4.761 0 9.298-1.436 13.177-4.162l13.661 14.208c.571.593 1.339.92 2.162.92.779 0 1.518-.297 2.079-.837a3.004 3.004 0 00.083-4.242zM23.984 6c9.374 0 17 7.626 17 17s-7.626 17-17 17-17-7.626-17-17 7.626-17 17-17z'/%3e%3c/svg%3e");
    background-size: 14px;
    background-repeat: no-repeat;
    background-position: 16px 48%;
    color: var(--theme-color);
    &::placeholder {
      font-family: var(--body-font);
      color: var(--inactive-color);
      font-size: 15px;
      font-weight: 500;
    }
  }
}

.header-profile {
  display: flex;
  align-items: center;
  padding: 0 16px 0 40px;
  margin-left: auto;
  flex-shrink: 0;
  svg {
    width: 22px;
    color: #f9fafb;
    flex-shrink: 0;
  }
}

.notification {
  position: relative;
  &-number {
    position: absolute;
    background-color: #3a6df0;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    font-size: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    right: -6px;
    top: -6px;
  }
  & + svg {
    margin-left: 22px;
    @media screen and (max-width: 945px) {
      display: none;
    }
  }
}

.profile-img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid var(--theme-color);
  margin-left: 22px;
}

.wide {
  .header-menu,
  .header-profile {
    display: none;
  }
  .search-bar {
    max-width: 600px;
    margin: auto;
    transition: 0.4s;
    box-shadow: 0 0 0 1px var(--border-color);
    padding-left: 0;
  }
  .menu-circle {
    margin-right: 0;
  }
}

.wrapper {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
}


.main-header {
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
  height: 58px;
  flex-shrink: 0;
  .header-menu {
    margin-left: 150px;
    flex: 1;
    @media screen and (max-width: 1055px) {
      margin: auto;
    }
    a {
      padding: 20px 24px;
    }
  }
}

.main-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  max-width: 1010px;
}

.menu-link-main {
  text-decoration: none;
  color: var(--theme-color);
  padding: 0 30px;
  @media screen and (max-width: 1055px) {
    display: none;
  }
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  color: var(--theme-color);
  padding: 20px 40px;
  height: 100%;
  overflow: auto;
  background-color: var(--theme-bg-color);
  @media screen and (max-width: 510px) {
    padding: 20px;
  }
  &.overlay {
    pointer-events: none;
    transition: 0.3s;
    background-color: var(--overlay-bg);
  }
}

.overlay-app {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  pointer-events: all;
  background-color: rgba(36, 39, 59, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  &.is-active {
    visibility: visible;
    opacity: 1;
  }
}

.img-content {
  font-weight: 500;
  font-size: 17px;
  display: flex;
  align-items: center;
  margin: 0;
  svg {
    width: 28px;
    margin-right: 14px;
  }
}

.content-text {
  font-weight: 400;
  font-size: 14px;
  margin-top: 16px;
  line-height: 1.7em;
  color: #ebecec;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.content-wrapper-context {
  max-width: 350px;
}

.content-button {
  background-color: #3a6df0;
  border: none;
  padding: 8px 26px;
  color: #fff;
  border-radius: 20px;
  margin-top: 16px;
  cursor: pointer;
  transition: 0.3s;
  white-space: nowrap;
}

.content-wrapper-img {
  width: 186px;
  object-fit: cover;
  margin-top: -25px;
  object-position: center;
  @media screen and (max-width: 570px) {
    width: 110px;
  }
}
.products {
  display: flex;
  align-items: center;
  width: 150px;
  @media screen and (max-width: 480px) {
    width: 120px;
  }
}
.status {
  margin-left: auto;
  width: 140px;
  font-size: 15px;
  position: relative;
  @media screen and (max-width: 700px) {
    display: none;
  }
  &-circle {
    width: 6px;
    height: 6px;
    background-color: #396df0;
    position: absolute;
    border-radius: 50%;
    top: 4px;
    left: -20px;
    &.green {
      background-color: #3bf083;
    }
  }
  &-button {
    font-size: 15px;
    margin-top: 0;
    padding: 6px 24px;
    @media screen and (max-width: 390px) {
      padding: 6px 14px;
    }
    &.open {
      background: none;
      color: var(--button-inactive);
      border: 1px solid var(--button-inactive);
    }
    &:not(.open):hover {
      color: #fff;
      border-color: #fff;
    }
  }
}

.content-button:not(.open):hover {
  background: #1e59f1;
}

.menu {
  width: 5px;
  height: 5px;
  background-color: var(--button-inactive);
  border-radius: 50%;
  box-shadow: 7px 0 0 0 var(--button-inactive),
    14px 0 0 0 var(--button-inactive);
  margin: 0 12px;
}

@media screen and (max-width: 415px) {
  .adobe-product .menu {
    display: none;
  }
}

.dropdown {
  position: relative;
  height: 53px;
  width: 40px;
  top: -24px;
  display: flex;
  left: -5px;
  background: transparent;
  border: none;
  cursor: pointer;
  ul {
    position: absolute;
    background: var(--dropdown-bg);
    height: 110px;
    width: 120px;
    right: 0;
    top: 20px;
    pointer-events: none;
    opacity: 0;
    transform: translatey(10px);
