<template>
  <div class="content-section">
    <div class="content-section-title"></div>
    <div class="apps-card lastest_card">
      <div class="app-card" v-for="(item, index) in packageList" :key="index" @click="goToPage(item.id,item.package_name)">
        <span>
          {{ item.package_name }}
        </span>
        <div class="app-card__subtext">
          <svg-item :svgList="item.svgs" :section="'lastest' + index"/>
        </div>
        <div class="app-card-buttons">
          <div class="option">
            <span><img src="@/assets/img/like_red.svg" alt="" width="25" height="25"> {{item.like_num}}</span>
            <span><img src="@/assets/img/collections.svg" alt="" width="25" height="25"> {{item.coll_num}}</span>
          </div>
          <div class="desc">
            {{ item.package_description }}
          </div>
          <div class="menu"></div>
        </div>
      </div>
    </div>
    <div class="loading_more">
      <p v-if="page<=total">
        <svg
          version="1.1"
          id="Layer_1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          x="0px"
          y="0px"
          width="24px"
          height="30px"
          viewBox="0 0 24 30"
          style="enable-background: new 0 0 50 50"
          xml:space="preserve"
          v-show="has_more"
        >
          <rect x="0" y="0" width="4" height="20" fill="#333">
            <animate
              attributeName="opacity"
              attributeType="XML"
              values="1; .2; 1"
              begin="0s"
              dur="0.6s"
              repeatCount="indefinite"
            />
          </rect>
          <rect x="7" y="0" width="4" height="20" fill="#333">
            <animate
              attributeName="opacity"
              attributeType="XML"
              values="1; .2; 1"
              begin="0.2s"
              dur="0.6s"
              repeatCount="indefinite"
            />
          </rect>
          <rect x="14" y="0" width="4" height="20" fill="#333">
            <animate
              attributeName="opacity"
              attributeType="XML"
              values="1; .2; 1"
              begin="0.4s"
              dur="0.6s"
              repeatCount="indefinite"
            />
          </rect>
        </svg>
        {{has_more?'...': ''}}
      </p>
      <p v-else>
        
      </p>
    </div>
  </div>
</template>

<script>

import { getSvgPackageList } from "@/network/svg/index";
import SvgItem from "@/components/SvgItem.vue";

export default {
  components: { SvgItem },
  data() {
    return {
      packageList: [],
      isLoading: false,
      page: 1,
      size: 12,
      total: 10,
      has_more: false,
      sign: true
    };
  },
  created() {
    this.getPackage();
  },
  mounted() {
    this.getMore()
  },
  methods: {
    getPackage() {
      const uid = "";
      const num = 8;
      this.isLoading = true
      if (this.page > this.total) {
        this.has_more = false
        return false
      }
      this.has_more = true
      getSvgPackageList(uid, num, this.page, this.size)
        .then((res) => {
          this.has_more = false
          this.sign = true
          if (res.status == 200) {
            this.isLoading = false
            let data = res.data.data
            this.packageList.push(...data);
            this.total = res.data.total
            if (this.page <= this.total) {
              this.page++
            }
          } else {
            this.$toast.show("");
          }
        })
        .catch((error) => {
          this.$toast.show("");
        });
    },
    getMore() {
      let timer = null;
      document.querySelector(".content-wrapper").addEventListener("scroll", (e) => {
        let scrollIn = parseInt(
          (document.querySelector(".content-wrapper").scrollTop /
            (document.querySelector(".content-wrapper").scrollHeight -
              document.querySelector(".content-wrapper").clientHeight)) *
            10
        );
        if (scrollIn > 9 && this.sign) {
          if (timer) clearTimeout(timer);
          timer = setTimeout(() => {
            this.sign = false
            this.getPackage();
          }, 500);
        }
      });
    },
    goToPage(pid, pName) {
      this.$EventBus.$emit('getPid', pid)
      const exist = this.$store.state.menuList.some(item => {return item.sign === 'mine'})
      if (exist) {
        for (let item of this.$store.state.menuList) {
          if (item.sign === 'mine') {
            item.pid = pid
            item.title = `-${pName}`
            item.active = true
          } else {
            item.active = false
          }
        }
      } else {
        this.$store.state.menuList.forEach(item => {
          item.active = false
        })
        this.$store.commit('SETMENULIST', {pid: pid,title: `-${pName}`, route: 'svgList', active: true, sign: 'mine'})
      }
      console.log('first')
      this.$router.push({name: 'svgList', params: {pid}}).catch()
    },
  },
};
</script>

<style lang="less" scoped>
.content-section {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  &-title {
    color: var(--content-title-color);
    margin-bottom: 14px;
  }
  ul {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    background-color: var(--content-bg);
    padding-left: 0;
    margin: 0;
    border-radius: 14px;
    border: 1px solid var(--theme-bg-color);
    cursor: pointer;
    li {
      list-style: none;
      padding: 10px 18px;
      display: flex;
      align-items: center;
      font-size: 16px;
      width: 100%;
      height: 100%;
      white-space: nowrap;
      transition: 0.3s;
      &:hover {
        background-color: var(--theme-bg-color);
        &:first-child {
          border-radius: 13px 13px 0 0;
        }
        &:last-child {
          border-radius: 0 0 13px 13px;
        }
      }
      & + li {
        border-top: 1px solid var(--border-color);
      }
    }
    svg {
      width: 28px;
      border-radius: 6px;
      margin-right: 16px;
      flex-shrink: 0;
    }
  }
}
.apps-card {
  // display: flex;
  column-count: 3;
  column-gap: 8px;
  // flex-wrap: wrap;
  width: calc(100% + 20px);
}
.app-card {
  // display: flex;
  // width: 100%;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
  flex-direction: column;
  // width: calc(33.3% - 20px);
  font-size: 16px;
  background-color: var(--content-bg);
  border-radius: 14px;
  border: 1px solid var(--theme-bg-color);
  padding: 20px;
  cursor: pointer;
  margin-bottom: 8px;
  &:hover {
    transform: scale(1.02);
    background-color: var(--theme-bg-color);
  }
  svg {
    width: 28px;
    border-radius: 6px;
    margin-right: 12px;
    flex-shrink: 0;
  }
  & + .app-card {
    // margin-left: 20px;
  }
  span {
    display: flex;
    align-items: center;
  }
  &__subtext {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.6em;
    // margin-top: 20px;
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 20px;
  }
  &-buttons {
    // display: flex;
    align-items: center;
    margin-left: auto;
    margin-top: 16px;
    .option {
      display: flex;
      margin-bottom: 4px;
      span {
        margin-right: 12px;
      }
    }
    .desc {
      font-size: 15px;
      color: var(--content-title-color);
    }
  }
  @media screen and (max-width: 1110px) {
    width: calc(50% - 20px);
    &:last-child {
      margin-top: 20px;
      margin-left: 0px;
    }
  }
  @media screen and (max-width: 565px) {
    width: calc(100% - 20px);
    margin-top: 20px;
    & + .app-card {
      margin-left: 0;
    }
  }
}
.loading_more {
  width: 100%;
  margin-top: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--content-title-color);
  p {
    width: 60%;
    height: 35px;
    background-color: var(--theme-bg-color);
    text-align: center;
    line-height: 35px;
    border-radius: 6px;
    font-size: 12px;
  }
  svg {
    color: var(--content-title-color);
    vertical-align: -6px;
    width: 15px;
    height: 15px;
  }
}
</style>
