<template>
  <div id="modal" v-if="visible == true">
    <div class="modal_wrapper" :style="{'width': `${width}px`}">
      <div class="top">{{ title }}</div>
      <div class="center">
        <slot></slot>
      </div>
      <div class="bottom">
        <slot name="bottom">
          <div @click="cancel"></div>
          <div @click="confirm"></div>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "",
    },
    visible: {
      type: Boolean,
      default: false,
    },
    width: {
      type: Number,
      default: 330
    }
  },
  methods: {
    cancel() {
      this.$emit("cancel");
    },
    confirm() {
      this.$emit("confirm");
    },
  },
};
</script>

<style lang="less" scoped>
#modal {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
  .modal_wrapper {
    background-color: #fff;
    border-radius: 12px;
    .top {
      margin-top: 18px;
      font-size: 20px;
      font-weight: bold;
      text-align: center;
      letter-spacing: 2px;
    }
    .center {
      margin: 15px 18px;
    }
    .bottom {
      display: flex;
      border-top: 1px solid #efefef;
      color: #678bbe;
      font-weight: bold;
      letter-spacing: 1px;
      div {
        flex: 1;
        padding: 18px;
        text-align: center;
        cursor: pointer;
      }
      div:first-child {
        font-weight: 500;
        border-right: 1px solid #efefef;
      }
    }
  }
}
</style>
