import axios from '../request'

// 
export const getSvgPackageInfo = (pid) => {
  return axios({
    url: 'api/svg/package/',
    params: {pid}
  })
}

// 
export const updateSvgPackageInfo = (pid, package_name, package_description, package_tag) => {
  return axios({
    url: `api/svg/package/update/${pid}`,
    method: 'put',
    data: {
      package_name,
      package_description,
      package_tag
    }
  })
}

// 
export const AddSvgPackage = (package_name, package_tag, package_description) => {
  return axios({
    url: `api/svg/package/add/`,
    method: 'post',
    data: {
      package_name,
      package_tag,
      package_description
    }
  })
}

// 
export const DelSvgPackage = (pid) => {
  return axios({
    url: `api/svg/package/delete/${pid}`,
    method: 'delete',
  })
}

// 
export const getSvgPackageList = (uid, num, page, size) => {
  return axios({
    url: 'api/svg/package/list/',
    params: {uid, num, page, size}
  })
}

// 
export const guessLike = () => {
  return axios({
    url: 'api/svg/package/guess/'
  })
}

// 
export const recommend = () => {
  return axios({
    url: 'api/svg/package/recommend/'
  })
}

// 
export const userAllPackage = (uid, page, size) => {
  return axios({
    url: 'api/svg/package/user/all/',
    params: {
      uid,
      page,
      size
    }
  })
}

// svg
export const getSvgList = (pid) => {
  return axios({
    url: 'api/svg/',
    params: {
      pid
    }
  })
}

// 
export const uploadSvg = (pid, author, svg_name, svg_code, pure_svg) => {
  return axios({
    url: 'api/svg/upload/',
    method: 'post',
    data: {
      package: pid,
      author,
      svg_name,
      svg_code,
      pure_svg
    }
  })
}

// 
export const deleteSvg = (pid) => {
  return axios({
    url: `api/svg/delete/${pid}`,
    method: 'delete',
  })
}