<template>
  <div class="title_bar" @click.once="isLoading">
    <input type="checkbox" :id="'toggler_' + checkBoxId">
    <label :for="'toggler_' + checkBoxId">
      <slot name="title"></slot>
      <span v-if="loading">
        <svg
          version="1.1"
          id="Layer_1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          x="0px"
          y="0px"
          width="24px"
          height="30px"
          viewBox="0 0 24 30"
          style="enable-background: new 0 0 50 50"
          xml:space="preserve"
          class="loading"
        >
          <rect x="0" y="0" width="4" height="20" fill="#333">
            <animate
              attributeName="opacity"
              attributeType="XML"
              values="1; .2; 1"
              begin="0s"
              dur="0.6s"
              repeatCount="indefinite"
            />
          </rect>
          <rect x="7" y="0" width="4" height="20" fill="#333">
            <animate
              attributeName="opacity"
              attributeType="XML"
              values="1; .2; 1"
              begin="0.2s"
              dur="0.6s"
              repeatCount="indefinite"
            />
          </rect>
          <rect x="14" y="0" width="4" height="20" fill="#333">
            <animate
              attributeName="opacity"
              attributeType="XML"
              values="1; .2; 1"
              begin="0.4s"
              dur="0.6s"
              repeatCount="indefinite"
            />
          </rect>
        </svg>
      </span>
      <span class="notification-number updates" v-show="!loading && clickSign">{{count}}</span>
    </label>
    <ul v-if="sign == 'package'">
      <li 
        v-for="item in packageList.slice(parseInt((page - 1)*size), parseInt(page*size))" 
        :key="item.id"
        @click="toSvgList(item.id, item.package_name, 'svgList', 'mine')">{{item.package_name}}</li>
      <li class="pagination">
        <span @click="perv">&lt;</span>
        <span @click="next">&gt;</span>
      </li>
    </ul>
    <ul v-else>
      <li 
        v-for="item in packageList.slice(parseInt((page - 1)*size), parseInt(page*size))" 
        :key="item.id"
        @click="toSvgList(item.package, item.coll_name, 'Collection', 'collection')"
        >{{item.coll_name}}</li>
      <li @click="addCollection" v-if="sign == 'mine'">+</li>
      <li class="pagination">
        <span @click="perv">&lt;</span>
        <span @click="next">&gt;</span>
      </li>
    </ul>
    <modal :title="''" :visible="visible" @cancel="cancel" @confirm="confirm">
      <Row class="info_item">
        <Col span="6" align="left" class="key"></Col>
        <Col span="18"><Input v-model="coll_name" placeholder="..." :maxlength="16"></Input></Col>
      </Row>
    </modal>
  </div>
</template>

<script>

import {userAllPackage} from '@/network/svg/index'
import {userCollectionList, addUserCollections} from '../../../network/operation/index'
import Modal from '../../../components/Modal'

export default {
  data() {
    return {
      packageList: [],
      page: 1,
      size: 6,
      total: 10,
      count: 0,
      loading: false,
      clickSign: false,
      visible: false,

      coll_name: '',
    }
  },
  props: {
    checkBoxId: {
      type: Number,
      default: 1
    },
    uid: {
      type: Number,
      default: -1
    },
    sign: {
      type: String,
      default: ''
    }
  },
  components: {
    Modal
  },
  created() {

  },
  methods: {
    getData() {
      if (this.page > this.total) {
        this.$toast.show("");
        return false
      }
      this.loading = true
      if (this.sign == 'package') {
        userAllPackage(this.uid, this.page, this.size).then(res => {
          if (res.status == 200) {
            this.packageList.push(...res.data.data)
            this.total = res.data.total
            this.count = res.data.count
            this.loading = false
          } else {
            this.$toast.show("");
          }
        }).catch(err => {
          this.$toast.show("");
        })
      } else if (this.sign == 'mine') {
        userCollectionList(1, this.page, this.size).then(res => {
          if (res.status == 200) {
            this.packageList.push(...res.data.data)
            this.total = res.data.total
            this.count = res.data.count
            this.loading = false
          } else {
            this.$toast.show("");
          }
        }).catch(err => {
          this.$toast.show("");
        })
      } else {
        userCollectionList(0, this.page, this.size).then(res => {
          if (res.status == 200) {
            this.packageList.push(...res.data.data)
            this.total = res.data.total
            this.count = res.data.count
            this.loading = false
          } else {
            this.$toast.show("");
          }
        }).catch(err => {
          this.$toast.show("");
        })
      }
    },
    isLoading() {
      this.clickSign = true
      this.$nextTick(() => {
        if (this.uid != -1) {
          this.getData()
        }
      })
      
    },
    perv() {
      if (this.page <= 1) {
        this.$toast.show("");
        return false
      }
      this.page--
    },
    next() {
      if (this.page >= this.total) {
        this.$toast.show("");
        return false
      }
      this.page++
      if (this.packageList.length < this.count) {
        this.getData()
      }
    },
    toSvgList(id, pName, routeName, sign) {
      if (sign == 'mine') {
        this.$EventBus.$emit('getPid', id)
      } else {
        this.$EventBus.$emit('getCid', id)
      }
      const exist = this.$store.state.menuList.some(item => {return item.sign === sign})
      if (sign == 'collection' && routeName == 'Collection') {
        routeName = 'svgList'
        this.$EventBus.$emit('getPid', id)
      }
      if (exist) {
        for (let item of this.$store.state.menuList) {
          if (item.sign === sign) {
            item.pid = id
            item.title = `${sign == 'mine'?'': ''}-${pName}`
            item.active = true
          } else {
            item.active = false
          }
        }
        
      } else {
        this.$store.state.menuList.forEach(item => {
          item.active = false
        })
        this.$store.commit('SETMENULIST', {pid: id,title: `${sign == 'mine'?'': ''}-${pName}`, route: routeName, active: true, sign: sign})
      }
      this.$router.push({name: routeName, params: {id, pName}}).catch(err => {})
    },
    addCollection() {
      this.visible = true
    },
    cancel() {
      this.visible = false
    },
    confirm() {
      addUserCollections(this.coll_name, this.uid).then(res => {
        this.visible = false
        if (res.status == 201) {
          this.$toast.show('');
          this.packageList = []
          this.page = 1
          this.getData();
        } else {
          this.$toast.show('');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "");
      })
    }
  }
}
</script>

<style lang="less" scoped>
label {
    text-decoration: none;
    color: var(--theme-color);
    display: flex;
    align-items: center;
    font-weight: 400;
    padding: 10px;
    font-size: 14px;
    border-radius: 6px;
    transition: 0.3s;
    cursor: pointer;
    &:hover {
      background-color: var(--hover-menu-bg);
    }
  }
.title_bar {
  ul {
    width: 88%;
    height: 0;
    float: right;
    text-align: left;
    transform: scaleY(0);
    transform-origin: top;
    opacity: 0;
    transition: transform .3s ease-in-out, opacity .3s ease-in-out, height .3s ease-in-out .1s;
    li {
      list-style: none;
      color: var(--theme-color);
      cursor: pointer;
      margin: 4px 0;
      padding: 6px 8px;
      overflow: hidden;
      text-overflow: ellipsis;
      border-radius: 6px;
      transition: 0.2s;
      &:hover {
        background-color: var(--hover-menu-bg);
      }
    }
    .pagination {
      &:hover {
        background-color: transparent;
      }
      span {
        padding: 0px 6px;
        border: 1px solid;
        border-radius: 5px;
        margin-right: 12px;
      }
    }
  }
  input[id^="toggler_"]:checked ~ ul {
    transform: scaleY(1);
    height: 100%;
    opacity: 1;
  }
}
.loading {
  width: 20px;
  height: 20px;
  vertical-align: -10px;
  margin-left: 8px;
}
input[id^="toggler_"] {
  display: none;
}
.notification-number {
  background-color: #3a6df0;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.updates {
  position: relative;
  top: 0;
  right: 0;
  margin-left: auto;
  width: 18px;
  height: 18px;
  font-size: 11px;
}
</style>