import Toast from './Toast'

const obj = {}

obj.install = (Vue) => {
  // console.log('toast');
  // 
  const toastContrustor = Vue.extend(Toast)
  //  new
  const toast = new toastContrustor()
  // 
  toast.$mount(document.createElement('div'))
  // toast.$eltoastdiv
  document.body.appendChild(toast.$el)

  Vue.prototype.$toast = toast
}

export default obj
