<template>
  <div class="block_package_item">
    <ul :class="{'recommend_style': section == 'recommend', 'lastest_style': section.indexOf(`lastest`) != -1}">
      <li v-for="item in svgList" :key="item.id" :style="svgList.length == 1?'width: 100%;display:flex;justify-content: center;': ''">
        <div :class="'shadow_'+section"></div>
      </li>
    </ul>
  </div>
</template>

<script>
import {shadow} from '../common/utils/utils'

export default {
  props: {
    section: {
      type: String,
      default: '',
    },
    svgList: {
      type: Array,
      default() {
        return []
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      if (!this.svgList.length) {
        return
      }
      const els = document.querySelectorAll(`.shadow_${this.section}`)
      let width = 125
      let height = 125
      if (this.svgList.length > 1) {
        width = 35
        height = 35
      }
      shadow(els, this.svgList, width, height)
    })
  },
  watch: {
    svgList: function (n, o) {
      this.$nextTick(() => {
        const els = document.querySelectorAll(`.shadow_${this.section}`)
        let width = 125
        let height = 125
        if (this.svgList.length > 1) {
          width = 35
          height = 35
        }
        shadow(els, this.svgList, width, height)
      })
    }
  },
}
</script>

<style lang="less" scoped>
ul {
  list-style: none;
  width: 100%;
  height: 140px;
  display: flex;
  flex-wrap: wrap;
  // justify-content: center;
  // align-items: center;
  cursor: pointer;
  li {
    text-align: center;
    float: left;
    padding: 14px;
  }
}
.recommend_style {
  li {
    padding: 15px;
  }
}
.lastest_style {
  width: inherit;
}
</style>