<template>
  <div>
    <div class="content-wrapper-header" style="align-item: none; position: relative;">
      <div class="content-wrapper-context">
        <h3 class="img-content"></h3>
        <div class="content-text" @click="goToPage('svgList')">
          <!-- <ul class="svg_pakage_list" v-for="item in svgPackage" :key="item.id"> -->
            <svg-item :svgList="svgPackage.svgs" :section="'recommend'" />

        </div>
      </div>
      <div class="author">
        <p class="p_name">{{svgPackage.package_name}}</p>
        <p class="description">
          {{svgPackage.package_description}}
        </p>
        <p>
          <img src="" alt="">
          <span class="u_name">{{svgPackage.author_name}}</span>
        </p>
        <p>
          <svg class="icon" style="width: 1em;height: 1em;vertical-align: middle;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2461"><path d="M725.333333 192c-89.6 0-168.533333 44.8-213.333333 115.2C467.2 236.8 388.266667 192 298.666667 192 157.866667 192 42.666667 307.2 42.666667 448c0 253.866667 469.333333 512 469.333333 512s469.333333-256 469.333333-512c0-140.8-115.2-256-256-256z" fill="#F44336" p-id="2462"></path></svg>
          &nbsp;&nbsp;{{svgPackage.like_num}}
        </p>
      </div>
      <img
        class="content-wrapper-img"
        src="https://assets.codepen.io/3364143/glass.png"
        alt=""
      />
    </div>
  </div>
</template>

<script>

import {recommend} from '@/network/svg/index'
import SvgItem from '@/components/SvgItem.vue'


export default {
  data() {
    return {
      svgPackage: []
    }
  },
  components: { SvgItem },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      recommend().then(res => {
        if (res.status == 200) {
         this.svgPackage = res.data[0]
        } else {
          this.$toast.show("");
        }
      }).catch(err => {
        this.$toast.show("");
      })
    },
    goToPage(pageName) {
      this.$store.commit('SETMENULIST', {title: `-${this.svgPackage.package_name}`, route: 'svgManager', active: true})
      this.$router.push({name: pageName, params: {pid: this.svgPackage.id}})
    }
  }
}
</script>

<style lang="less" scoped>
.content-wrapper {
  display: flex;
  flex-direction: column;
  color: var(--theme-color);
  padding: 20px 40px;
  height: 100%;
  overflow: auto;
  background-color: var(--theme-bg-color);
  @media screen and (max-width: 510px) {
    padding: 20px;
  }
  &-header {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    background-image: url("https://www.transparenttextures.com/patterns/cubes.png"),
      linear-gradient(
        to right top,
        #cf4af3,
        #e73bd7,
        #f631bc,
        #fd31a2,
        #ff3a8b,
        #ff4b78,
        #ff5e68,
        #ff705c,
        #ff8c51,
        #ffaa49,
        #ffc848,
        #ffe652
      );
    border-radius: 14px;
    padding: 20px 40px;
    @media screen and (max-width: 415px) {
      padding: 20px;
    }
  }
  &.overlay {
    pointer-events: none;
    transition: 0.3s;
    background-color: var(--overlay-bg);
  }
}
.content-wrapper-context {
  max-width: 350px;
}
.content-text {
  width: 72%;
}

.content-button {
  background-color: #3a6df0;
  border: none;
  padding: 8px 26px;
  color: #fff;
  border-radius: 20px;
  margin-top: 16px;
  cursor: pointer;
  transition: 0.3s;
  white-space: nowrap;
}

.content-wrapper-img {
  width: 186px;
  object-fit: cover;
  margin-top: -25px;
  object-position: center;
  @media screen and (max-width: 570px) {
    width: 110px;
  }
}
.img-content {
  font-weight: 500;
  font-size: 17px;
  display: flex;
  align-items: center;
  margin: 0;
  svg {
    width: 28px;
    margin-right: 14px;
  }
}
.svg_pakage_list {
  display: flex;
  width: 200px;
  height: 100%;
  .svg_item {
    list-style: none;
    svg {
      width: 2.5em;
      height: 2.5em;
      margin: 8px 14px;
    }
  }
}
.author {
  position: absolute;
  left: 28%;
  width: 45%;
  height: 80%;
  border-left: 1px solid #ccc;
  padding-left: 20px;
  p {
    margin-bottom: 6px;
  }
  .p_name {
    font-size: 30px!important;
  }
  .description {
    font-size: 14px;
    color: #dfdfdf;
  }
  .p_name {
    font-size: 20px;
    margin-top: 16px;
  }
}
</style>