import Vue from "vue";
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  token: '',
  uid: '',
  menuList: [
    {
      pid: '-1',
      title: '',
      route: 'index',
      active: 'true',
      sign: 'index'
    }
  ]
}

const store = new Vuex.Store({
  state,
  mutations: {
    GETTOKEN(state, payload) {
      state.token = payload
    },
    GETUID(state, payload) {
      state.uid = payload
    },
    SETMENULIST(state, payload) {
      state.menuList.push(payload)
    },
    CHANGESTATUS(state, payload) {
      state.menuList[payload.index].active = payload.status
    }
  }
})

export default store