import axios from '../request'

// token
export const getToken = (username, password) => {
  return axios({
    url: 'api/login/',
    method: 'post',
    data: {
      username,
      password
    }
  })
}

// 
export const getUserInfo = (token) => {
  return axios({
    url: 'api/userInfo/',
    params: {token}
  })
}

// 
export const register = (data) => {
  return axios({
    url: 'api/user/register/',
    method: 'post',
    data,
  })
}

// 
export const reset = (data) => {
  return axios({
    url: 'api/user/reset/',
    method: 'patch',
    data,
  })
}
// 
export const updateAvatar = (avatar) => {
  return axios({
    url: 'api/user/update/',
    method: 'patch',
    data: {
      avatar
    },
  })
}