<template>
  <div class="content-section">
    <div class="content-section-title"></div>
    <div data-v-a8cdf146="" class="content-section" style="overflow: hidden;-moz-user-select:none;margin-top: 0" unselectable="on" onselectstart="return false;">
      <div data-v-a8cdf146="" class="apps-card" style="flex-wrap: nowrap;-moz-user-select:none;transition: all .6s ease;" unselectable="on" onselectstart="return false;" >
        <div data-v-a8cdf146="" class="app-card" v-for="(item, index) in packageList" :key="item.id">
          <span data-v-a8cdf146="" @click="goToPage(item.id,item.package_name)">
            {{item.package_name}}
          </span>
          <div data-v-a8cdf146="" class="app-card__subtext" @click="goToPage(item.id,item.package_name)">
            <svg-item :svgList="item.svgs" :section="'guess_like' + index" />
          </div>
          <div data-v-a8cdf146="" class="app-card-buttons author_info">
             <div>
               <p class="author">
                 <img src="@/assets/img/title_bg.jpg" alt="" width="20" height="20">
                 <span>{{item.author_name}}</span>
               </p>
               <p class="like">
                 <svg class="icon" style="width: 1.5em;height: 1.5em;vertical-align: text-bottom;fill: currentColor;overflow: hidden;" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2461"><path d="M725.333333 192c-89.6 0-168.533333 44.8-213.333333 115.2C467.2 236.8 388.266667 192 298.666667 192 157.866667 192 42.666667 307.2 42.666667 448c0 253.866667 469.333333 512 469.333333 512s469.333333-256 469.333333-512c0-140.8-115.2-256-256-256z" fill="#F44336" p-id="2462"></path></svg>
                 {{item.like_num}}
               </p>
             </div>
             <div>
               <p class="tag">
                 {{item.package_tag}}
               </p>
               <p class="count">
                  {{item.count}}
               </p>
             </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {guessLike} from '@/network/svg/index'
import SvgItem from '@/components/SvgItem.vue'

export default {
  data() {
    return {
      packageList: [],
      move: {},
    }
  },
  components: {
    SvgItem
  },
  created() {
    this.getPackage()
  },
  methods: {
    // 
    getPackage() {
      const uid = ''
      const num = 8
      guessLike().then(res => {
        if (res.status == 200) {
          this.packageList = res.data
        } else {
          this.$toast.show('');
        }
      }).catch(err => {
        this.$toast.show("");
      })
    },
    goToPage(pid, pName) {
      this.$EventBus.$emit('getPid', pid)
      const exist = this.$store.state.menuList.some(item => {return item.sign === 'mine'})
      if (exist) {
        for (let item of this.$store.state.menuList) {
          if (item.sign === 'mine') {
            item.pid = pid
            item.title = `-${pName}`
            item.active = true
          } else {
            item.active = false
          }
        }
      } else {
        this.$store.state.menuList.forEach(item => {
          item.active = false
        })
        this.$store.commit('SETMENULIST', {pid: pid,title: `-${pName}`, route: 'svgList', active: true, sign: 'mine'})
      }
      this.$router.push({name: 'svgList', params: {pid}}).catch(err => {})
    }
  },
};
</script>

<style lang="less" scoped>
.content-section {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  &-title {
    color: var(--content-title-color);
    margin-bottom: 14px;
  }
  ul {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    background-color: var(--content-bg);
    padding-left: 0;
    margin: 0;
    border-radius: 14px;
    border: 1px solid var(--theme-bg-color);
    cursor: pointer;
    li {
      list-style: none;
      padding: 10px 18px;
      display: flex;
      align-items: center;
      font-size: 16px;
      width: 100%;
      height: 100%;
      white-space: nowrap;
      transition: 0.3s;
      &:hover {
        background-color: var(--theme-bg-color);
        &:first-child {
          border-radius: 13px 13px 0 0;
        }
        &:last-child {
          border-radius: 0 0 13px 13px;
        }
      }
      & + li {
        border-top: 1px solid var(--border-color);
      }
    }
    svg {
      width: 28px;
      border-radius: 6px;
      margin-right: 16px;
      flex-shrink: 0;
    }
  }
}
@media screen and (max-width: 415px) {
  .adobe-product .menu {
    display: none;
  }
}
.products {
  display: flex;
  align-items: center;
  width: 150px;
  @media screen and (max-width: 480px) {
    width: 120px;
  }
}
.status {
  margin-left: auto;
  width: 140px;
  font-size: 15px;
  position: relative;
  @media screen and (max-width: 700px) {
    display: none;
  }
  &-circle {
    width: 6px;
    height: 6px;
    background-color: #396df0;
    position: absolute;
    border-radius: 50%;
    top: 4px;
    left: -20px;
    &.green {
      background-color: #3bf083;
    }
  }
  &-button {
    font-size: 15px;
    margin-top: 0;
    padding: 6px 24px;
    @media screen and (max-width: 390px) {
      padding: 6px 14px;
    }
    &.open {
      background: none;
      color: var(--button-inactive);
      border: 1px solid var(--button-inactive);
    }
    &:not(.open):hover {
      color: #fff;
      border-color: #fff;
    }
  }
}
.main-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.menu-link-main {
  text-decoration: none;
  color: var(--theme-color);
  padding: 0 30px;
  @media screen and (max-width: 1055px) {
    display: none;
  }
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  color: var(--theme-color);
  padding: 20px 40px;
  height: 100%;
  overflow: auto;
  background-color: var(--theme-bg-color);
  @media screen and (max-width: 510px) {
    padding: 20px;
  }
  &.overlay {
    pointer-events: none;
    transition: 0.3s;
    background-color: var(--overlay-bg);
  }
}

.dropdown {
  position: relative;
  height: 53px;
  width: 40px;
  top: -24px;
  display: flex;
  left: -5px;
  background: transparent;
  border: none;
  cursor: pointer;
  ul {
    position: absolute;
    background: var(--dropdown-bg);
    height: 110px;
    width: 120px;
    right: 0;
    top: 20px;
    pointer-events: none;
    opacity: 0;
    transform: translatey(10px);
    transition: all 0.4s ease;
    li a {
      text-decoration: none;
      color: var(--theme-color);
      font-size: 12px;
    }
  }
}

.dropdown.is-active {
  ul {
    opacity: 1;
    pointer-events: all;
    transform: translatey(25px);
    li:hover {
      background-color: var(--dropdown-hover);
    }
  }
}

.button-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 187px;
  margin-left: auto;
  @media screen and (max-width: 480px) {
    width: auto;
  }
}

.pop-up {
  position: absolute;
  padding: 30px 40px;
  top: 50%;
  left: 50%;
  transform: (translate(-50%, -50%));
  overflow-y: auto;
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.4);
  transition: all 0.3s;
  z-index: 10;
  background-color: var(--popup-bg);
  width: 500px;
  visibility: hidden;
  opacity: 0;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  white-space: normal;
  @media screen and (max-width: 570px) {
    width: 100%;
  }
  &.visible {
    visibility: visible;
    opacity: 1;
  }
  &__title {
    padding-bottom: 20px;
    border-bottom: 1px solid var(--border-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__subtitle {
    white-space: normal;
    margin: 20px 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.8em;
    a {
      color: var(--theme-color);
    }
  }
}
.apps-card {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: calc(100% + 20px);
}
.app-card {
  display: flex;
  flex-direction: column;
  width: calc(33.3% - 20px);
  font-size: 16px;
  background-color: var(--content-bg);
  border-radius: 14px;
  border: 1px solid var(--theme-bg-color);
  padding: 20px;
  cursor: pointer;
  transition: 0.3s ease;
  &:hover {
    transform: scale(1.02);
    background-color: var(--theme-bg-color);
  }
  svg {
    width: 28px;
    border-radius: 6px;
    margin-right: 12px;
    flex-shrink: 0;
  }
  & + .app-card {
    margin-left: 20px;
  }
  span {
    display: flex;
    align-items: center;
  }
  &__subtext {
    width: 205px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.6em;
    // margin-top: 20px;
    border-bottom: 1px solid var(--border-color);
    // padding-bottom: 20px;
  }
  &-buttons {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-top: 16px;
  }
  @media screen and (max-width: 1110px) {
    width: calc(50% - 20px);
    &:last-child {
      margin-top: 20px;
      margin-left: 0px;
    }
  }
  @media screen and (max-width: 565px) {
    width: calc(100% - 20px);
    margin-top: 20px;
    & + .app-card {
      margin-left: 0;
    }
  }
}
.author_info {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  margin-top: 8px;
  margin-left: 0!important;
  p {
    margin-top: 5px;
  }
  .author {
    display: flex;
    img {
      border-radius: 50%;
      border: 2px solid #ccc;
      vertical-align: text-bottom;
      margin-right: 5px;
    }
  }
  .like {
    .icon {
      margin-right: 2px;
    }
  }
  .tag {
    text-align: right;
  }
  .count {
    text-align: right;
  }
}
.apps-card {
  overflow: auto;
  padding-bottom: 10px;
  .app-card:last-child {
    margin-right: 20px;
  }
}
.apps-card::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}
.apps-card::-webkit-scrollbar-thumb {
  background: var(--scrollbar-bg);
  border-radius: 10px;
}
</style>
