import Vue from "vue";
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const Index = () => import('@/views/index/IndexContent.vue')
const Setting = () => import('@/views/account/Setting.vue')
const SvgList = () => import('@/views/svgViews/SvgList.vue')
const SvgManager = () => import('@/views/svgManager/SvgManager.vue')
const Collection = () => import('@/views/collection/Collection.vue')

const routes = [
  {
    path: '',
    name: 'index',
    component: Index
  },
  {
    path: '/setting',
    name: 'userSetting',
    component: Setting
  },
  {
    path: '/svglist',
    name: 'svgList',
    component: SvgList
  },
  {
    path: '/svgmanager',
    name: 'svgManager',
    component: SvgManager
  },
  {
    path: '/collection',
    name: 'Collection',
    component: Collection
  },
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router