import axios from '../request'

// 
export const like = (pid, uid) => {
  return axios({
    url: 'api/like/',
    params: {pid, uid}
  })
}
// 
export const likeCount = () => {
  return axios({
    url: 'api/like/count/',
  })
}

// 
export const userCollectionList = (sign, page, size) => {
  return axios({
    url: 'api/collection/list/',
    params: {sign, page, size}
  })
}

// 
export const addUserCollections = (coll_name, author, packageId) => {
  return axios({
    url: 'api/collection/add/',
    method: 'post',
    data: {
      coll_name, 
      author, 
      'package': packageId
    }
  })
}

// 
export const editUserCollections = (packageId, coll_name, author) => {
  return axios({
    url: `api/collection/edit/${packageId}`,
    method: 'put',
    data: {
      coll_name,
      author
    }
  })
}

// 
export const delUserCollections = (packageId) => {
  return axios({
    url: `api/collection/del/${packageId}`,
    method: 'delete'
  })
}

// svg
export const collectionSvg = (collection, svg) => {
  return axios({
    url: `api/collection/svg/add/`,
    method: 'post',
    data: {
      collection,
      svg
    }
  })
}

// svg
export const collectionSvgList = (collection) => {
  return axios({
    url: `api/collection/svg/list/`,
    params: {
      collection
    }
  })
}

// 
export const packageCollectionCount = () => {
  return axios({
    url: `api/collection/count/`,
  })
}
