            <span @click="accountForm = 'signup'"></span>
            <span @click="accountForm = 'forget'"></span>
          </p>
          <button type="button" @click="accountLogin"></button>
        </div>
        <div class="signup_container" v-show="accountForm == 'signup'">
          <input
            type="text"
            placeholder=""
            v-model="signupForm.username"
          />
          <input type="text" placeholder="" v-model="signupForm.email" />
          <input
            type="password"
            placeholder=""
            v-model="signupForm.password"
          />
          <input
            type="password"
            placeholder=""
            v-model="signupForm.passwordConfirm"
          />
