            <span @click="accountForm = 'login'"></span>
          </p>
          <div style="width: 100%; text-align: center">
            <button type="button" @click="accountSignup"></button>
          </div>
        </div>
        <div class="forget_pwd_container" v-show="accountForm == 'forget'">
          <input type="text" placeholder="" v-model="resetPwdForm.username" />
          <input type="text" placeholder="" v-model="resetPwdForm.email" />
          <input type="password" placeholder="" v-model="resetPwdForm.password" />
          <input type="password" placeholder="" v-model="resetPwdForm.passwordConfirm" />
          <p>
            <span @click="accountForm = 'login'"></span>
          </p>
          <div style="width: 100%; text-align: center">
            <button type="button" @click="resetPwd"></button>
          </div>
        </div>
      </div>
