</template>

<script>
import { Notice, Message } from "iview";

import { getToken, getUserInfo, register, reset } from "@/network/index/index";
import SidePackageList from './childComps/SidePackageList.vue'

export default {
  data() {
    return {
      account: {
        id: "",
        username: "",
        avatar: "",
      },
      loginForm: {
        username: "",
        password: "",
      },
      signupForm: {
        username: "",
        email: "",
        password: "",
        passwordConfirm: "",
      },
      resetPwdForm: {
        username: "",
        email: "",
        password: "",
        passwordConfirm: "",
      },
      userForm: 0,
      accountForm: "login",
      packageList: [],
      userPacPage: 1,
      userPacSize: 8,
      userPacTotal: 10,
      userPacCount: 0
    };
  },
  components: {
    SidePackageList
  },
  created() {
    if (JSON.parse(localStorage.getItem("userInfo"))) {
      const { id, username, avatar } = JSON.parse(
        localStorage.getItem("userInfo")
      );
      this.account.id = id;
      this.account.username = username;
      this.account.avatar = avatar;
      this.$store.commit('GETUID', id)
    }
  },
  methods: {
    // 
    accountLogin() {
      const { username, password } = this.loginForm;
      if (username == "") {
        Notice.warning({ title: "" });
        return;
      }
      if (password == "") {
        Notice.warning({ title: "" });
        return;
      }
      getToken(username, password)
        .then((res) => {
          if (res.status == 200) {
            localStorage.setItem("token", res.data.access);
            this.getInfo(res.data.access);
          } else {
            this.$toast.show("");
          }
        })
        .catch((err) => {
          this.$toast.show("");
        });
    },
    // 
    getInfo(token) {
      getUserInfo(token)
        .then((res) => {
          if (res.status == 200) {
            this.account.id = res.data[0].id;
            this.account.username = res.data[0].username;
            this.account.avatar = res.data[0].avatar;
            const userInfo = JSON.stringify({
              id: res.data[0].id,
              username: res.data[0].username,
              avatar: res.data[0].avatar,
            });
            localStorage.setItem("userInfo", userInfo);
            Message.success("");
            this.$EventBus.$emit('isLogin')
          } else {
            this.$toast.show("");
          }
        })
        .catch((err) => {
          this.$toast.show("");
        });
    },
    // 
    accountSignup() {
      if (this.validate() == false) {
        return;
      }
      const { username, email, password } = this.signupForm;
      const data = {
        username,
        email,
        password,
      };
      register(data)
        .then((res) => {
          if (res.status == 201) {
            Message.success("");
            this.loginForm.username = username;
            this.loginForm.password = password;
            this.accountForm = "login";
          } else {
            this.$toast.show("");
          }
        })
        .catch((err) => {
          this.$toast.show("");
        });
    },
    // 
    resetPwd() {
      const {username, email, password, passwordConfirm} = this.resetPwdForm
      const data = {
       username,
       email,
       password
      }
      if (password != passwordConfirm) {
        Notice.warning({ title: "" });
        return false
      }
      if (password.length < 8) {
        Notice.warning({ title: "8" });
        return false
      }
      reset(data).then(res => {
        if (res.status == 200) {
          Message.success(res.data.message)
          this.accountForm = 'login'
        } else {
          Message.error(res.data.message || '')
        }
      }).catch(err => {
        Message.error(err.response.data.message || '')
      })
    },
    // 
    validate() {
      const { username, email, password, passwordConfirm } = this.signupForm;
      if (username == "") {
        Notice.warning({ title: "" });
        return false;
      }
      if (username.length < 4 || username.length > 32) {
        Notice.warning({ title: "4~32" });
        return false;
      }
      if (email == "") {
        Notice.warning({ title: "" });
        return false;
      }
      const reg = /^([a-zA-Z\d])(\w|\-)+@[a-zA-Z\d]+\.[a-zA-Z]{2,4}$/;
      if (!reg.test(email)) {
        Notice.warning({ title: "" });
        return false;
      }
      if (password == "") {
        Notice.warning({ title: "" });
        return false;
      }
      if (password.length < 8) {
        Notice.warning({ title: "8" });
        return false;
      }
      if (password != passwordConfirm) {
        Notice.warning({ title: "" });
        return false;
      }
    },
    // 
    goToPage(pageName, sign) {
      const exist = this.$store.state.menuList.some(item => {return item.sign === sign})
      if (exist) {
        for (let item of this.$store.state.menuList) {
          if (item.sign === sign) {
            item.active = true
          } else {
            item.active = false
          }
        }
        
      } else {
        this.$store.commit('CHANGESTATUS', {index: 0, status: false})
        this.$store.commit('SETMENULIST', {title: `${sign == 'manage'?'': ''}`, route: pageName, active: true, sign: sign})
      }
      this.$router.push({name: pageName}).catch(err => {})
    }
  },
};
</script>

